*,
*:before,
*:after {
  -webkit-box-sizing: inherit;
  box-sizing: inherit;
  margin: 0;
  padding: 0;
}
img {
  display: block;
  max-width: 100%;
}
a {
  text-decoration: none;
  color: inherit;
  cursor: pointer;
}
button {
  background-color: transparent;
  color: inherit;
  border-width: 0;
  padding: 0;
  cursor: pointer;
}
figure {
  margin: 0;
}
input::-moz-focus-inner {
  border: 0;
  padding: 0;
  margin: 0;
}
ul,
ol,
dd {
  margin: 0;
  padding: 0;
  list-style: none;
}
h1,
h2,
h3,
h4,
h5,
h6 {
  margin: 0;
  font-size: inherit;
  font-weight: inherit;
}
p {
  margin: 0;
}
cite {
  font-style: normal;
}
fieldset {
  border-width: 0;
  padding: 0;
  margin: 0;
}
input,
textarea,
button,
select,
a {
  -webkit-tap-highlight-color: transparent !important;
  outline: none;
  font-family: inherit;
  font-size: inherit;
}
html,
body,
#root,
.app {
  height: auto;
  min-height: 100vh;
}
html {
  font-size: 62.5%;
}
body {
  font-family: "Open Sans", sans-serif;
  font-size: 1.6rem;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  margin: 0;
  padding: 0;
}
.dark {
  background-color: #000718;
  color: #fff;
}
.container {
  max-width: 117rem;
  padding: 0 1.6rem;
  margin: 0 auto;
}
.header {
  background-color: transparent;
  position: absolute;
  top: 0;
  right: 0;
  left: 0;
}
.header__navbar {
  height: 6rem;
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.header__logo {
  font-weight: 600;
  font-size: 2.4rem;
}
.header__list {
  display: flex;
  flex: 1;
  justify-content: flex-end;
}
.header__list li {
  margin-left: 1.6rem;
}
.header__list li i {
  font-size: 2rem;
}
.header__unit-icon {
  font-weight: 700;
  font-size: 2rem;
  line-height: 2rem;
  letter-spacing: 1.15px;
}
.header__favorite-icon {
  position: relative;
}
.header__favorite-num {
  position: absolute;
  top: 6px;
  left: 12px;
  border-radius: 100px;
  height: 16px;
  width: 16px;
  background-color: #ff9e18;
  text-align: center;
  font-size: 10px;
  line-height: 17px;
  font-family: "Open Sans", sans-serif;
}
.hero {
  background-position: bottom center;
  background-repeat: no-repeat;
  background-size: auto;
  height: 60vh;
  display: flex;
  align-items: center;
  justify-content: center;
}
@media (max-width: 480px) {
  .hero {
    background-size: cover;
  }
}
.hero .search {
  position: relative;
}
.hero .search__input {
  border: none;
  padding: 0.8rem 1.6rem;
  line-height: 3.6rem;
  border-radius: 10rem;
  font-size: inherit;
  width: 400px;
  text-transform: capitalize;
  transition: all 0.3s;
}
@media (max-width: 480px) {
  .hero .search__input {
    width: 240px;
  }
}
.hero .search__input:focus {
  box-shadow: 0px 4px 8px rgba(0, 0, 0, 0.15);
  transition: all 0.6s;
}
.hero .search__btn {
  position: absolute;
  right: 1.6rem;
  bottom: 1rem;
  top: 1rem;
  padding-left: 1rem;
  color: #000;
  background-color: #fff;
}
.hero .search__results {
  position: absolute;
  right: 0;
  left: 0;
  top: 6rem;
  background-color: #fff;
  padding: 1.6rem;
  max-height: 30vh;
  overflow-y: auto;
  z-index: 10;
  box-shadow: 0px 2px 2px rgba(0, 0, 0, 0.25);
  color: #000;
}
.hero .search__results li {
  padding: 0.8rem;
  cursor: pointer;
  border-bottom: 2px solid #eee;
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.hero .search__results li:hover {
  background-color: #f5b153bf;
}
.hero .search__results li:last-child {
  border-bottom: none;
}
.hero .search__label {
  font-size: 1.2rem;
  color: gray;
  font-weight: 300;
}
.weather-forecast__list {
  display: flex;
  justify-content: space-between;
  padding: 1.6rem 0 3.2rem;
}
@media (max-width: 480px) {
  .weather-forecast__list {
    display: flex;
    flex-flow: column;
  }
}
.weather-forecast__item {
  text-align: center;
  padding: 1rem;
  box-shadow: 0px 2px 8px rgba(0, 0, 0, 0.08);
  border-radius: 1.6rem;
  flex-basis: 15%;
}
@media (max-width: 480px) {
  .weather-forecast__item {
    text-align: center;
    padding: 1rem 0;
    box-shadow: 0px 2px 8px rgba(0, 0, 0, 0.08);
    border-radius: 1.6rem;
    display: flex;
    margin-bottom: 1.6rem;
    flex-basis: 30px;
    align-items: center;
  }
  .weather-forecast__item :nth-child(n + 3) {
    flex-grow: 1;
  }
  .weather-forecast__item > p {
    flex-basis: 50px;
  }
}
.weather-forecast__day {
  font-weight: 600;
  text-transform: uppercase;
}
.weather-forecast__current {
  width: 300px;
  display: flex;
  justify-content: space-evenly;
}
.weather-current {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.weather-current__title {
  font-size: 6.4rem;
}
.weather-current__today {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  position: relative;
  top: -50px;
}
.weather-current__btn {
  border: 1px solid;
  border-radius: 2rem;
  padding: 0.4rem 0.8rem;
  font-size: 1.4rem;
  margin: 1rem;
}
.favorites {
  padding-top: 8rem;
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(20rem, 2fr));
  grid-auto-rows: auto;
  grid-gap: 2rem;
}
.favorites__item {
  border: 1px solid;
  border-radius: 5px;
  padding: 8px;
}
.loading-wrapper {
  position: absolute;
  top: 0;
  right: 0;
  left: 0;
  bottom: 0;
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: 1000;
  background-color: #ff9e18;
  transition: 5s ease-in-out infinite;
}
.cloudy {
  animation: cloudy 5s ease-in-out infinite;
  background: #FFFFFF;
  border-radius: 50%;
  box-shadow: #FFFFFF 60px -15px 0 -5px, #FFFFFF 25px -25px, #FFFFFF 30px 10px, #FFFFFF 60px 15px 0 -10px, #FFFFFF 85px 5px 0 -5px;
  height: 50px;
  width: 50px;
  position: relative;
  right: 30px;
}
.cloudy:after {
  animation: cloudy_shadow 5s ease-in-out infinite;
  background: #000000;
  border-radius: 50%;
  content: '';
  height: 15px;
  width: 120px;
  opacity: 0.2;
  position: absolute;
  left: 5px;
  bottom: -60px;
  transform: scale(0.7);
}
@keyframes cloudy {
  50% {
    transform: translateY(-20px);
  }
}
@keyframes cloudy_shadow {
  50% {
    transform: translateY(20px) scale(1);
    opacity: .05;
  }
}
